import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

import Intro from '../pages/intro/Index.vue'
import TestPage from '../pages/testpage/Index.vue'
import Result from '../pages/result/Index.vue'


const routes = [
  { 
      path: '/', 
      component: Intro 
  },
  { 
      path: '/testpage', 
      component: TestPage 
  },
  { 
      path: '/result', 
      component: Result 
  },
]

const router = new VueRouter({
  routes 
})

export default router