<template>
    <div class="result">
        <div class="result__content">

            <div class="result__titlewrap">
                <div class="result__title">Ваш результат: <span>{{getResultCount}}</span></div>
            </div>
    
    
            <table class="result__table striped">
                <thead>
                <tr>
                    <th>#</th>
                    <th>Вопрос</th>
                    <th>Правильность ответа</th>
                </tr>
                </thead>

                <tbody>
                    <tr v-for="(item, index) in getResultsList" :key="index">
                        <td v-html="index+1"></td>
                        <td v-html="item.question"></td>
                        <td >
                            <p class="result__right teal-text text-lighten-3" v-if="item.right">Правильно</p>
                            <p class="result__right red-text text-lighten-2" v-else>Ошибка</p>
                        </td>
                    </tr>
                </tbody>
            </table>
            
            <div class="waves-effect waves-light btn" @click='clickHandler'>Пройти тест сначала</div>

        </div>
    </div>
</template>

<script>
    import {mapGetters, mapMutations} from 'vuex'

    export default {
        name:'ResultPage',
        computed:{
            ...mapGetters(['getResultsList','getResultCount'])
        },
        methods:{
            ...mapMutations(['resetResult']),

            clickHandler(){
                this.resetResult()
                this.$router.push('/')
            }
        }
    }
</script>

<style lang="scss" scoped>

</style>