<template>
    <div class="testpage">
        <div class="testpage__content">

            <div class="testpage__card">

                <div class="testpage__titlewrap">
                    <p class="testpage__title">Вопрос:</p>
                </div>

                <div class="testpage__textwrap">
                    <p class="testpage__text" v-html="currentQuestion.question"></p>
                </div>

                 <div class="testpage__titlewrap">
                    <p class="testpage__title">Варианты ответа:</p>
                </div>

                <div class="testpage__options">
                    <div class="testpage__options-item" 
                         v-for="(item,index) in currentQuestion.options" 
                         :key="index" 
                         v-html="item.text"
                         @click="clickHandler(item)"
                    ></div>
                </div>

            </div>

        </div>
    </div>
</template>

<script>
    import {mapGetters, mapMutations} from 'vuex'

    export default {
        name:'TestPage',
        computed:{
            ...mapGetters(['getQuestionList'])
        },
        mounted(){
            this.currentQuestion = this.getQuestionList[0]
        },
        data(){
            return{
                currentQuestion:{},
                index:0,
            }
        },
        methods:{
            ...mapMutations(['addResultItem']),

            clickHandler(item){
                this.addResultItem({
                    question:this.currentQuestion.question,
                    result:item.right,
                })
                 
                if(this.index<(this.getQuestionList.length-1)){

                    this.index+=1

                    this.currentQuestion = this.getQuestionList[this.index]

                } else{
                    this.$router.push('/result')
                }
            }
        }
    }
</script>

